.bodyPage div,
.bodyPage span,
.bodyPage tr,
.bodyPage td,
.bodyPage th,
.bodyPage p {
  /* H3 Desktop 18 pt */

  font-family: "GothamRnd-Book2";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
}
.questionContent {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  overflow-wrap: anywhere;
}
.questionName {
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #233584;
}
.mathzoneMultipleChoiceFlexBox {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  width: 90%;
  margin-top: 1rem;
  cursor: pointer;
}
.mathzoneMultipleChoiceFlexBox > div {
  width: calc(50% - 0.5rem);
  max-width: 100%;
  flex: 1;
  display: flex;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  align-items: center;
  padding-left: 18px;
  color: #233584;
  border-radius: 12px;
  word-break: break;
  min-height: auto;
  height: 60px;
  gap: 2rem;
  border: 1px solid black;
  height: auto;
}

.mathzoneSelectedChoiceType {
  background-color: #b9c2fc;
}

.mathzone-circle-selectbox {
  min-width: 40px;
  aspect-ratio: 1/1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 50%;
  border: 1.5px solid lightgray;
}

.mathzoneMainOlBox {
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
  width: 100%;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  gap: 2rem;
  border-radius: 5px;
}

.mathzoneMainOlBox > div {
  border: 1px solid black;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  border-radius: 5px;
  justify-content: center;
}

.mathzoneHorizontalolBox {
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.mathzoneHorizontalolBox > div {
  border: 1px solid black;
  padding: 5px;
  border-radius: 5px;
}
